/* ============================================
   RESET CSS + アクセシビリティ対応
   ============================================ */
/* 全要素を border-box に統一（余計なサイズ計算を避ける） */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* デフォルトの余白をリセット */
body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

ul,
ol {
  padding: 0;
  list-style: none; /* 必要な場合は個別に list-style を戻す */
}

/* スクロール挙動を滑らかに（ユーザー体験改善） */
html {
  scroll-behavior: smooth;
}

/* 本文の基本設定 */
body {
  min-height: 100vh;
  text-rendering: optimizeLegibility; /* 字形を見やすく */
  line-height: 1.5; /* 読みやすい行間 */
  font-family: system-ui, sans-serif;
}

/* リンクの基本リセット */
a {
  color: inherit;
  text-decoration: none;
  cursor: pointer;
}

/* ホバー・アクティブ状態 */
a:hover,
a:active {
  color: inherit;
  text-decoration: none;
}

/* フォーカス状態（アクセシビリティ） */
a:focus-visible {
  outline: 2px solid #3498db;
  outline-offset: 2px;
}

/* 画像は親要素の幅にフィットさせる */
img {
  max-width: 100%;
  display: block;
  height: auto;
}

/* 記事内要素の間隔を自動で確保 */
article > * + * {
  margin-top: 1em;
}

/* ============================================
   フォーム要素リセット
   ============================================ */
/* フォントを継承・不要なデフォルト装飾を外す */
input,
button,
select,
textarea {
  font: inherit;
  background: transparent;
  border: none;
  border-radius: 0;
  padding: 0;
  margin: 0;
}

/* appearance: none は必要なものだけに限定 */
.input-text,
.input-email,
.input-tel,
.textarea-box {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

/* フォーカスリングは必ず残す（outline: none は禁止） */
:focus-visible {
  outline: 2px solid #3498db; /* キーボード操作用の青枠 */
  outline-offset: 2px;
}

/* ボタンやセレクトボックスはカーソルを pointer に */
input[type=submit],
input[type=button],
button,
select,
label {
  cursor: pointer;
}

/* 無効状態をわかりやすく */
input[disabled],
button[disabled] {
  cursor: not-allowed;
  opacity: 0.6;
}

/* テキストエリアは縦方向のみリサイズ可能に */
textarea {
  resize: vertical;
}

/* ============================================
   アクセシビリティ対応
   ============================================ */
/* スキップリンク（キーボードユーザー用） */
.skip-link {
  position: absolute;
  top: -40px;
  left: 6px;
  background: #000;
  color: #fff;
  padding: 8px;
  text-decoration: none;
  z-index: 1000;
}

.skip-link:focus {
  top: 6px;
}

/* 必須フィールドをラベルにマーク表示 */
label.required::after {
  content: " *";
  color: #e74c3c;
}

/* エラーメッセージのスタイル */
.error-message {
  color: #e74c3c;
  font-size: 0.9em;
}

/* エラー状態は入力中にだけ強調 */
/* ラジオ・チェックボックスは視覚的に隠すが、スクリーンリーダーでは有効 */
input[type=radio],
input[type=checkbox] {
  position: absolute;
  opacity: 0;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  clip: rect(0 0 0 0);
  white-space: nowrap;
  border: 0;
}

/* 代替UI（label側）にフォーカスリングを表示 */
input[type=radio]:focus + label,
input[type=checkbox]:focus + label {
  outline: 2px solid #3498db;
  outline-offset: 2px;
}

/* ============================================
   fieldset / legend リセット（フォーム枠の完全解除）
   ============================================ */
fieldset {
  border: 0;
  margin: 0;
  padding: 0;
  min-inline-size: 0; /* Firefoxの横ズレ防止 */
}

legend {
  padding: 0;
  margin: 0;
  font-weight: inherit;
  font-size: inherit;
  line-height: inherit;
}

